<template>
  <div id="app">

    <AppTemplate
      slotted
      applicationName="社内検索エンジン">

      <form
        v-bind:class="{large_margins: !message}"
        v-on:submit.prevent="search()">
        <input class="search_bar" type="search" v-model="query" placeholder='例： "174000"　や "山田"　や　"ＦＦＲ"'>
        <span class="mdi mdi-magnify search_button" v-on:click="search()"/>
      </form>

      <div class="search_results_wrapper">

        <div class="message">{{message}}</div>

        <!-- filters -->
        <div class="filters_wrapper" v-if="labels.length > 0">
          <div class="" v-for="(label, index) in labels">
            <label>{{label}}</label>
            <input type="checkbox" v-bind:value="label" v-model="filters">
          </div>

          <span class="icon_button mdi mdi-filter-outline" v-on:click="filter_all_out()"/>
          <span class="icon_button mdi mdi-filter" v-on:click="filter_all_in()"/>

        </div>

        <SearchResult
          v-for="result in results"
          v-if="filters.includes(result._fields[result._fields.length-1].labels[0])"
          v-bind:data="result"
          v-bind:query="query"
          v-bind:filters="filters"
          v-on:updateLabelList="fill_label_list($event)"/>
      </div>

    </AppTemplate>

  </div>
</template>

<script>

import AppTemplate from './components/jtekt_vue_template/AppTemplate.vue'
import SearchResult from './components/SearchResult.vue'

export default {
  name: 'app',
  components: {
    AppTemplate,
    SearchResult
  },
  data(){
    return {
      query: "",
      results: [],
      message: "",
      labels: [],
      filters: [],
    }
  },
  methods: {
    search(){
      this.message = "Searching...";

      // Clear everything
      this.results.splice(0, this.results.length)
      this.labels.splice(0, this.labels.length)
      this.filters.splice(0, this.filters.length)

      this.axios.post('http://search.mike.jtekt.maximemoreillon.com/search', {
        query: this.query
      })
      .then(response => {
        this.results = response.data;
        if(this.results.length < 1) this.message = "No result"
        else this.message = "Found " + this.results.length + " match(es)";

        // fill labels filter list after clearing
        this.fill_label_list(this.results);

      })
      .catch((error) => console.log(error));
    },
    fill_label_list(records){
      // Does not get corrected when closing related nodes but that's OK
      records.forEach(record => {
        let label = record._fields[record._fields.length-1].labels[0]

        if(!this.labels.includes(label)) {
          this.labels.push(label)

          // filter in by default
          this.filters.push(label)
        }
      })
    },
    filter_all_in(){
      this.filters.splice(0, this.filters.length)
      this.labels.forEach(label => {
        this.filters.push(label)
      })
    },
    filter_all_out(){
      this.filters.splice(0, this.filters.length)
    },
  }
}
</script>

