  margin: 25px;

  /* Layouting of search bar and search button */
  display: flex;
  justify-content: center;
  align-items: center;

  transition: margin 0.5s;

}

form > * {
  margin: 5px;
  padding: 5px;
}

form.large_margins {
  margin: 30vh 0;
}

.search_bar{
  width: 50vw;

  font-size: 120%;

  border: none;
  border-bottom: 2px solid #444444;
  outline: none;

  transition: border-color 0.25s;
}

.search_bar:focus {
  border-bottom: 2px solid #c00000;
}

.search_button, .icon_button {
  font-size: 150%;
  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
  transition: color 0.25s;
}
.search_button:hover, .icon_button:hover {
  color: #c00000;
}
.filters_wrapper{
  margin: 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.filters_wrapper > * {
  margin: 0 5px;
}

.message {
  text-align: center;
}

.search_results_wrapper{
  margin: 25px auto;
  max-width: 800px;
  overflow-x: hidden;

}
</style>
