<template>
  <div class="application_wrapper">

    <header>
      <!-- burger to open nav if mobile view -->
      <span
        v-if="navigation.length > 0"
        class="mdi navigation_control control"
        v-bind:class="navigation_control_icon"
        v-on:click="toggle_navigation()"/>

      <!-- Logo and app name -->
      <img class="rotating_logo" src="./img/logo/thick/logo.svg" alt="">
      <span class="application_name">{{applicationName}}</span>

      <!-- right side: login controls -->
      <span class="mdi mdi-account aligned_right control" v-on:click="show_profile()"/>
      <span class="mdi mdi-logout control" v-on:click="logout()"/>

    </header>

    <div class="columns_wrapper">
      <!-- left column: nav -->

      <div
        class="nav_background"
        v-on:click="toggle_navigation()"
        v-bind:class="{visible: navigation_open}"/>

      <nav
        v-if="navigation.length > 0"
        v-bind:class="{open: navigation_open}">
        <!-- NAV items must be passed as props -->
        <router-link
          v-for="(navigationItem, index) in navigation"
          v-bind:key="index"
          v-bind:to="navigationItem.route">
          <span
            class="mdi"
            v-bind:class="'mdi-' + navigationItem.icon"/>
          {{navigationItem.label}}
        </router-link>

      </nav>

      <!-- right column: main and footer -->
      <main>
        <router-view v-if="!slotted" class="view"/>
        <slot v-else class="view"></slot>
        <footer>
          <img class="rotating_logo" src="./img/logo/thick/logo.svg" alt="">
          <div class="application_info">
            <div class="application_name">{{applicationName}}</div>
            <div class="author_name">Maxime MOREILLON</div>
          </div>
        </footer>
      </main>

    </div>

  </div>
</template>

<script>
export default {
  name: 'AppTemplate',
  props: {
    applicationName: {
      type: String,
    },
    slotted: {
      type: Boolean,
      default() {
        return false;
      }
    },
    navigation: {
      type: Array,
      default(){
        return []
      },
    },
  },
  data(){
    return {
      navigation_open: false,
    }
  },
  methods: {
    toggle_navigation(){
      this.navigation_open = !this.navigation_open
    },
    show_profile(){
      window.location.href = "http://employees.mike.jtekt.maximemoreillon.com/";
    },
    logout(){

      // TODO: Add check for login
      this.axios.post('http://authentication.mike.jtekt.maximemoreillon.com/logout')
      .then( () => location.reload())
      .catch(error => console.log(error))
    },
  },
  computed: {
    navigation_control_icon(){
      if(this.navigation_open) return "mdi-backburger"
      else return "mdi-menu"
    }
  }
}
</script>

<!-- Not scoped so as to access body and app -->
<style>
* {
  box-sizing: border-box;
}

body {
  margin: 0;

  /* TEXT */
  /* font */
  font-family: Helvetica, Meiryo, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* default text color */
  color: #111111;

}

.application_wrapper{

  /* default text color */
  color: #111111;

  /* Take all the vertical viewport space */
  height: 100vh;

  /* vertical layout */
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.rotating_logo {
  animation-name: logo_rotation;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes logo_rotation {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}

}

/* HEADER */
header {
  /* header size must be fixed */
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;

  background-color: #444444;
  color: white;

  font-size: 150%;

  display: flex;
  align-items: stretch;

  /* shadow */
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);

  /* necessary for shadows on nav */
  position: relative;
  z-index: 11;
}

header > * {
  display: flex;
  align-items: center;

  margin: 10px;
  /* keep only one margin width between elements */
  margin-right: 0px;
}

header > *:last-child {
  margin-right: 10px;
}

header .rotating_logo {
  width: 35px;
}

header .aligned_right{
  margin-left: auto
}

header .control {
  font-size: 120%;
  cursor: pointer;
}

header .control:hover {
  color: #dddddd;
}

header .navigation_control{
  transform: translateX(-200%);
  transition: transform 0.5s;
}

.columns_wrapper{
  /* used to position nav when mobile view*/
  position: relative;

  /* take all avvailable vertical space */
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0;

  /* WHY IS THIS WORKING? */
  overflow-y: auto;

  /* Get columns side by side */
  display: flex;
  flex-direction: row;
}

/* NAV */
nav {

  /* enable scroll on overflow */
  overflow-y: auto;


  /* for the border not to hit ends */
  /* NOT THE BEST WAY TO DO IT */
  margin: 15px 0;

  width: 200px;
  flex-basis: 200px;
  flex-shrink: 0;
  flex-grow: 0;

  display: flex;
  flex-direction: column;
  align-items: stretch;
  border-right: 1px solid #dddddd;

  transition: transform 0.5s;

  background-color: white; /* might be better defined somewhere else */

}

nav > * {

  font-size: 120%;
  text-align: center;

  /* inter-element spacing */
  padding: 15px 0;

  /* behave slike a link so re-apply text attributes */
  text-decoration: none;
  color: #111111;

  transition: border-color 0.25s;

  border-right: 3px solid transparent;
}

nav a:hover {
  border-right: 3px solid #666666;
}
nav a.router-link-exact-active {
  border-right: 3px solid #c00000;
}

nav .mdi {
  /* space between icon and text */
  margin-right: 5px;
}

/* must not be placed inside nav because of transform */
.nav_background {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s, visibility 0.5s;
}


main {
  /* contains router view and footer */

  /* enable scroll on overflow */
  /* NOT WORKING UNLESS COLUMNS WRAPPER ALSO HAS IT */
  /* NO IDEA WHY */
  overflow-y: auto;

  /* grow horizontally (and shrink if necessary) */
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0;

  /* take all available vertical space */
  /* NOT WORKING ON IE */
  /*
  display: flex;
  flex-direction: column;
  align-items: stretch;
  */

}

main .view {
  /* DOES NOT APPLY WELL TO SLOT */
  padding: 15px;

  /* NOT WORKING ON IE */
  /*
  flex-grow: 1;
  flex-shrink: 0;
  */
}


footer {

  /* NOT WORKING ON IE */
  /*
  flex-grow: 0;
  flex-shrink: 0;
  */

  /* clearing the nav bar and leaving space for the border top*/
  margin: 0 10%;

  border-top: 1px solid #dddddd;

  padding: 15px;

  display: flex;
  justify-content: center;
  align-items: center;

  /* might be some alignement fixing to do */

}

footer .rotating_logo {
  width: 40px;
}

footer .application_info{
  margin-left: 10px;
}

@media only screen and (max-width: 800px) {


  nav {
    /* transform margin into padding */
    margin: 0;
    padding: 15px 0;

    border: none;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    height: 100%; /* WHY DOES THIS WORK ? */
    transform: translateX(-100%);
  }

  nav.open{
    transform: translateX(0%);
  }

  .nav_background.visible {
    opacity: 0.5;
    visibility: visible;;
  }

  header .navigation_control{
    transform: translateX(0%);

  }
}

</style>
