<template>
  <div class="search_result">

    <!-- the node itself -->
    <div class="node_itself" v-bind:class="data._fields[data._fields.length-1].labels[0]">
      <div class="label_container">
        {{data._fields[data._fields.length-1].labels[0]}}
      </div>

      <table>
        <tr
        v-for="property, key in data._fields[data._fields.length-1].properties"
        v-if="key !== 'password_hashed'">
          <td>{{key}}</td>
          <td>
            <span v-bind:class="{highlighted: highlighted(property)}">
              {{property}}
            </span>
          </td>
        </tr>
      </table>


      <div class="related_nodes_button_container">
        <span
          v-bind:class="related_nodes_button_class"
          class="related_nodes_button mdi"
          v-on:click="toggle_related_nodes()"/>
      </div>

    </div>

    <!-- related noted -->
    <div class="related_nodes_wrapper" v-if="related_nodes_open && related_nodes.length > 0">

      <div
        class="related_node_container"
        v-for="related_node in related_nodes"
        v-if="filters.includes(related_node._fields[related_node._fields.length-1].labels[0])">

        <!-- relationship is in field 1 -->
        <div class="relationship_wrapper">{{related_node._fields[1].type}}</div>

        <!-- the related nodes -->
        <SearchResult
          class="related_node"
          v-bind:data="related_node"
          v-bind:filters="filters"
          v-on:updateLabelList="$emit('updateLabelList',$event)"/>


      </div>

    </div>
    <div class="related_nodes_message" v-else-if="related_nodes_open && related_nodes_loading">
      Loading...
    </div>
    <div
      class="related_nodes_message"
      v-else-if="related_nodes_open && !related_nodes_loading && related_nodes.length == 0">
      No result
    </div>


  </div>
</template>

<script>
export default {
  name: 'SearchResult',
  props: {
    data: Object,
    query: String,
    filters: Array,
  },
  mounted(){
    //console.log(this.filters)
  },
  data(){
    return {
      related_nodes: [],
      related_nodes_open : false,
      related_nodes_loading: false,
    }
  },

  methods: {
    toggle_related_nodes(){
      if(!this.related_nodes_open){
        this.related_nodes_open = true;
        this.find_related_nodes()
      }
      else this.related_nodes_open = false

    },
    find_related_nodes(){
      this.related_nodes_loading = true;
      this.related_nodes.splice(0,this.related_nodes.length)
      this.axios.post('http://search.mike.jtekt.maximemoreillon.com/find_related_nodes', {
        // Getting the last inde xbecause corresponds to related node
        id: this.data._fields[this.data._fields.length-1].identity.low
      })
      .then((response) => {
        this.related_nodes = response.data;
        this.related_nodes_loading = false;
        this.$emit('updateLabelList', this.related_nodes)
      } )
      .catch((error) => console.log(error));
    },
    highlighted(property) {

      if(this.query){
        return String(property).toLowerCase().indexOf(this.query.toLowerCase()) != -1;
      }
      else {
        return false;
      }


    },
  },
  computed: {
    related_nodes_button_class(){
      if(!this.related_nodes_open) return "mdi-magnify-plus-outline"
      else return "mdi-magnify-minus-outline"

    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.search_result {
}

.node_itself{
  margin-top: 15px;
  border: 2px solid red;
  border-radius: 5px;
  padding: 15px;
}

.Employee{ border-color: #c00000;}
.Division{ border-color: navy;}
.Department{ border-color: mediumblue;}
.Section{ border-color: royalblue;}
.Group{ border-color: deepskyblue;}
.Workplace{ border-color: forestgreen;}
.Report{ border-color: lightslategrey;}
.Computer{ border-color: darkslategrey;}

.label_container {
  font-weight: bold;
  text-align: left;
}

table {
  margin-top: 25px;
  margin-bottom: 25px;
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

tr:not(:last-child){
  border-bottom: 1px solid #ddd;
}

td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

td:first-child {
  width: 50%;
}

.highlighted {
  background-color: yellow;
}

.related_nodes_button_container {
  text-align: center;
}

.related_nodes_button{
  font-size: 200%;
  cursor: pointer;
  transition: color 0.25s;
}

.related_nodes_button:hover{
  color: #c00000;
}

.related_nodes_message {
  text-align: center;
  margin: 5px;
}

.related_node_container{

  margin-left: 25px;

  display: flex;

  border-left: 2px solid #444444;
}




.relationship_wrapper {

  flex-basis: 100px;
  flex-grow: 0;
  flex-shrink: 0;

  height: 75px;
  border-bottom: 2px solid #444444;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 5px;
  font-size: 75%;
}

.related_node{
  flex-grow: 1;
  flex-shrink: 1;
}


</style>
